import React, { useEffect, useState } from "react";
// Redux staff actions, hooks, selectors
import { get_brands, get_subcategories } from "../redux/actions/products";
import { getData } from "./../redux/utils/products";
import { useDispatch, useSelector } from "react-redux";
import {
	selectProducts,
	selectCategories,
} from "./../redux/selectors/products";

// React Router
import { useParams } from "react-router-dom";
// Translation
import MiddleFitlers from "../components/Products/MiddleFitlers";
import SubcategoriesRow from "../components/Products/SubcategoriesRow";
import FiltersRow from "../components/Products/FiltersRow";
import BigHeader from "../components/Products/BigHeader";
import Breadcumb from "../components/Products/Breadcumb";
import { useTranslation } from "react-i18next";
import ProductsMap from "../components/Products/ProductsMap";

// take redux store data
const mapState = (state) => ({
	products: selectProducts(state),
	prev: state.products.prev,
	next: state.products.next,
	count: state.products.count,
	categories: selectCategories(state),
});

function Products() {
	const { i18n } = useTranslation();
	const [subCategories, setSubCategories] = useState([]);
	const [brands, setBrands] = useState([]);
	const [choosedSubCategories, setChoosedSubCategories] = useState([]);
	const [searchedCategory, setSearchedCategory] = useState(null);
	const [choosedBrand, setChoosedBrand] = useState([]);
	const [loadMore, setLoadMore] = useState(null);
	const [clickedLoadMore, setClickedLoadMore] = useState(false);
	const [middleFilters, setMiddleFilters] = useState(null);
	const [isLoading, setIsLoading] = useState(false);
	const [choosedOrder, setChoosedOrder] = useState({
		name: "default",
		value: "",
	});

	// Take url param
	const { category_slug } = useParams();
	// Take redux info
	const { products, count, categories } = useSelector(mapState);
	const dispatch = useDispatch();

	useEffect(() => {
		window.scrollTo({
			top: 0,
			behavior: "smooth",
		});
		setSearchedCategory(null);
		setSubCategories([]);
		setBrands([]);
	}, [category_slug]);

	useEffect(() => {
		// if we have categories finding that category that was searched

		if (Array.isArray(categories) && categories.length >= 1) {
			setSearchedCategory(
				categories.find((category) => category.slug === category_slug)
			);
		}
		// if found that category load products and subcategory with this id
		if (searchedCategory) {
			if (clickedLoadMore) {
				setClickedLoadMore(false);
			} else {
				getData(
					dispatch,
					setIsLoading,
					setLoadMore,
					loadMore,
					products,
					choosedSubCategories,
					choosedBrand,
					searchedCategory,
					choosedOrder,
					i18n.language
				);
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [
		searchedCategory,
		categories,
		choosedOrder,
		choosedSubCategories,
		choosedBrand,
		clickedLoadMore,
		i18n.language,
	]);

	useEffect(() => {
		// console.log("Trying get subs and brands");
		if (searchedCategory) {
			dispatch(get_subcategories(searchedCategory?.id, i18n.language)).then(
				(result) => {
					setSubCategories(result);
					// console.log("Got subs ", result);
				}
			);
			dispatch(get_brands(searchedCategory?.id, i18n.language)).then(
				(result) => {
					setBrands(result);
					// console.log("Got brands ", result);
				}
			);
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [i18n.language, searchedCategory]);

	return (
		<>
			<BigHeader
				searchedCategoryName={
					isLoading ? (
						<div class="spinner-border text-white" role="status">
							<span class="visually-hidden">Loading...</span>
						</div>
					) : (
						searchedCategory?.name
					)
				}
			/>

			<div className="container">
				<Breadcumb
					topmar={true}
					searchedCategoryName={searchedCategory?.name}
				/>

				<SubcategoriesRow
					subCategories={subCategories}
					choosedSubCategories={choosedSubCategories}
					setChoosedSubCategories={setChoosedSubCategories}
				/>

				<FiltersRow
					count={count}
					middleFilters={middleFilters}
					setMiddleFilters={setMiddleFilters}
					choosedOrder={choosedOrder}
					setChoosedOrder={setChoosedOrder}
				/>

				{middleFilters && (
					<MiddleFitlers
						middleFilters={middleFilters}
						brands={brands}
						setChoosedBrand={setChoosedBrand}
						choosedBrand={choosedBrand}
					/>
				)}

				<ProductsMap
					setClickedLoadMore={setClickedLoadMore}
					setLoadMore={setLoadMore}
					isLoading={isLoading}
				/>
			</div>
		</>
	);
}

export default Products;
